#loading {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    font-size: 40px;
    color: #5bc0de;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60px;
    z-index: 9999;
    .fa-spinner {
        font-size: 50px;
    }
}

